module.exports = ({ services, stores, lib }) => async tagData => {
    const pipeline = Object.values(services.tagDataPipeline)
    const tag = await lib.pipeAsync(pipeline, tagData)

    return stores.tags.insert(tag, tagId => {
        const { onChange } = stores.tags.id(tagId)
        onChange('name', services.tags.propagateTagName(tagId))
        onChange('roleId', services.tags.propagateRoleId(tagId))
        services.tags.adjustInstanceCounts()
    })
}
