module.exports = ({ lib, stores, components }) => () => {
    const $noticeContent = document.createElement('div')
    $noticeContent.innerHTML =
        'Do you <mark>really</mark> need more than one active avatar? <a>Learn more</a>'

    const $notice = components.notice($noticeContent)
    const $learnMore = $noticeContent.querySelector('a')

    $learnMore.addEventListener('click', () => {
        stores.app.setState({ modal: 'multiples' })
    })

    const noticeVisibility = lib.visibilityToggle($notice)

    stores.options.onChange('active', ({ active }) => {
        active <= 1 ? noticeVisibility.hide() : noticeVisibility.show()
    })

    return $notice
}
