const EventEmitter = require('events')

module.exports = () => () => {
    let nextId = 1
    const index = {}
    const collectionEvents = new EventEmitter()

    const emptyItem = { getState: () => null }

    const find = id => index[id] || emptyItem
    const scalar = () => find(insert({})) // pick props
    const getItems = () => Object.values(index).map(({ getState }) => getState())
    const getState = id => find(id).getState()
    const setState = (id, changes) => find(id).setState(changes)

    //const onChange = (id, listenerMap) => find(id).onChange(listenerMap);

    const onChange = (key, id, callback) => {
        find(id).onChange(key, callback)
    }

    const onAnyChange = _onAnyChange(collectionEvents)
    const onInsert = listener => collectionEvents.on('insert', listener)
    const onBeforeRemove = listener => collectionEvents.on('beforeRemove', listener)

    const insert = (data, callback) => {
        const id = nextId++
        const state = { id, ...data }
        const itemEvents = new EventEmitter()
        const getState = () => state
        const setState = _setState(state, [itemEvents, collectionEvents])
        // const onChange = _onChange(itemEvents);

        const _onChange = eventEmitter => (key, listener) => {
            key.split(' ').forEach(key => {
                eventEmitter.on(key, listener)
            })

            listener(state)
        }

        const onChange = _onChange(itemEvents)
        index[id] = {
            state,
            getState,
            setState,
            onChange,
            initialise: setState,
        }
        if (callback) callback(id)
        collectionEvents.emit('insert', id)
        return id
    }

    const remove = id => {
        collectionEvents.emit('beforeRemove', id)
        delete index[id]
    }

    const id = id => {
        const { onChange } = find(id)
        return { onChange }
    }

    return {
        id,
        scalar,
        onInsert,
        onChange,
        onAnyChange,
        onBeforeRemove,
        insert,
        remove,
        setState,
        getState,
        getItems,
        ids: () => {
            return Object.keys(index)
        },
    }
}

const _setState = (state, eventEmitters) => changes => {
    Object.entries(changes).forEach(([key, val]) => {
        state[key] = val
        eventEmitters.forEach(eventEmitter => eventEmitter.emit(key, state))
    })
}

const _onAnyChange = eventEmitter => listenerMap => {
    const listen = ([key, listener]) => eventEmitter.on(key, listener)
    Object.entries(listenerMap).forEach(listen)
}
