module.exports = ({ constants, stores, services, analytics }) => () => {
    services.system.initialiseState(constants.initialState)

    constants.options.modes.forEach(mode => {
        stores.options.onChange(mode, services.tags.adjustInstanceCounts)
    })

    services.system.initialiseKeyBindings()

    if (constants.analytics.google) analytics.google()
    if (constants.analytics.fullstory) analytics.fullstory()
}
