module.exports = ({ constants, components }) => () => {
    const $fallbacks = document.createElement('div')

    const fallbackOptions = constants.gravatar.fallbacks.map(components.gravatar.fallback)
    $fallbacks.append(...fallbackOptions)

    const labelText =
        'Select a generated image style to use in case profile image is not found.'
    return components.label(labelText, $fallbacks)
}
