module.exports = ({ components }) => ({ done }) => {
    const $gravatar = document.createElement('div')
    $gravatar.title = 'Gravatar'
    $gravatar.className = 'gravatar'

    $gravatar.append(
        components.gravatar.freetext(),
        components.gravatar.fallbacks(),
        components.gravatar.actions({ done })
    )

    return $gravatar
}
