// eslint-disable-next-line require-await
module.exports = ({ lib }) => async input => {
    const { imageBlob, ...remainingData } = input
    if (!imageBlob) return remainingData
    if (!imageBlob.type.startsWith('image/')) return { error: 'not an image' }
    const image = await lib.readAsDataUrl(imageBlob)
    return { image, ...remainingData }
}
