const wire = require('./wire')
const constants = require('./constants')

const libraries = {
    analytics: require('./analytics'),
    lib: require('./lib'),
    stores: require('./stores'),
    services: require('./services'),
    components: require('./components'),
    styles: require('./styles'),
}

const wiring = {
    analytics: '',
    lib: 'global',
    stores: 'lib',
    services: 'analytics context stores constants lib global',
    styles: 'services stores lib',
    components: 'context styles services stores constants lib',
}

const globalOrig = global

const createInstance = ({ name, global = globalOrig }) => {
    const deps = {
        global,
        constants,
        context: {
            currentInstanceName: name,
            ...context,
        },
    }

    const instance = wire(libraries, wiring, deps)
    instance.services.system.initialise()

    context.instances.push({ name, instance })
    /* console.debug('A new instance of Agile Avatars was created', { name });
       console.debug({ [name]: instance }); */

    return instance
}

const context = {
    createInstance,
    instances: [],
}

module.exports = context
