module.exports = ({ context }) => () => {
    const $tip = document.createElement('div')

    $tip.title = 'Blocked and other badges'

    $tip.innerHTML = `
        <p>Create blocked and other badges in the same way as avatars.</p>
    `

    const insert = async url => {
        const tag = await app.services.tagDataSources.imageUrl(url)
        console.log({ tag })
        app.services.tags.insertTag(tag)
    }

    const app = context.createInstance({ name: 'badges-tip' })
    app.stores.options.setState({ shape: 'square' })
    insert('img/sample-badges/BLOCKED.png')
    insert('img/sample-badges/AT RISK.png')

    const $tagList = app.components.tagList()
    const $appContainer = app.components.appContainer($tagList)
    $tip.append($appContainer)

    return $tip
}
