module.exports = ({ lib, stores, services }) => () => {
    const $imageUploadOptions = document.createElement('div')
    $imageUploadOptions.className = 'image-upload-options'

    const $dragAndDropImages = document.createElement('span')
    $dragAndDropImages.textContent = 'Drag & drop images'

    const $chooseImages = document.createElement('a')
    $chooseImages.className = 'choose-images'
    $chooseImages.textContent = 'Choose images'

    $chooseImages.addEventListener('click', e => {
        e.preventDefault()
        $fileInput.click()
    })

    const $fileInput = document.createElement('input')
    $fileInput.type = 'file'
    $fileInput.multiple = true
    $fileInput.accept = 'image/*'
    $fileInput.style.display = 'none'

    const insert = async file => {
        const tag = await services.tagDataSources.imageFile(file)
        services.tags.insertTag(tag)
    }

    $fileInput.addEventListener('change', () => {
        Array.from(lib.getFiles($fileInput)).forEach(insert)
    })

    const $importImagesFromGravatar = document.createElement('a')
    $importImagesFromGravatar.className = 'import-images-from-gravatar'
    $importImagesFromGravatar.textContent = 'Import images from Gravatar'

    $importImagesFromGravatar.addEventListener('click', () => {
        stores.app.setState({ modal: 'gravatarImport' })
    })

    $imageUploadOptions.append(
        $dragAndDropImages,
        '●',
        $chooseImages,
        $fileInput,
        '●',
        $importImagesFromGravatar
    )

    return $imageUploadOptions
}
